import React, { useRef, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Select from "react-select";
import axios from "axios";
import { useEffect } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const CreateServiceLayer = () => {
  const [service, setService] = useState({
    category: "",
    selectedService: "",
    actualPrice: "",
    discountedPrice: "",
    platformFees: 99,
    note: "",
    shiftTimes: [{ day: "", from: "", to: "" }],
    doctor: [],
    reportingTime: "",
    description: "",
    // date: "",
  });
  const [status, setStatus] = useState("");

  const centreId = localStorage.getItem("centreId");
  const navigate = useNavigate();

  const [categories, setCategories] = useState([]);
  const selectedCategory = categories.find(
    (cat) => cat._id === service.category
  );

  const [categoryService, setCategoryService] = useState({
    category: "",
    services: [],
    message:"",
  });
  

  const [doctors, setDoctors] = useState([]);

  const doctorOptions = [
    { value: "add_doctor", label: "Add New Doctor" },
    ...doctors.map((doc) => ({
      value: doc._id,
      label: doc.name,
    })),
  ];

  const selectedDoctorOptions = doctorOptions.filter((opt) =>
    service.doctor.includes(opt.value)
  );

  const handleDoctorChange = (selected) => {
    if (!selected) {
      setService((prev) => ({
        ...prev,
        doctor: [],
      }));
      return;
    }

    const selectedValues = selected.map((opt) => opt.value);

    // Check if "add_doctor" is selected
    if (selectedValues.includes("add_doctor")) {
      // Redirect to create doctor page
      navigate("/create-doctor");
      return;
    }

    // Set selected doctor IDs
    setService((prev) => ({
      ...prev,
      doctor: selected.map((opt) => opt.value), // array of ObjectIds
    }));
  };

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/category/getAllCategories`
        );
        setCategories(res.data);
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    };
    fetchCategories();
  }, []);

  useEffect(() => {
    const fetchDoctors = async () => {
      if (!centreId) {
        console.error("Centre ID not found in localStorage");
        return;
      }
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/doctors/centre/${centreId}`
        );
        setDoctors(res.data);
      } catch (error) {
        console.error("Error fetching doctors:", error);
      }
    };
    fetchDoctors();
  }, []);

  const handleCategoryChange = async (e) => {
    const selectedCategoryId = e.target.value;
    // Find selected category by ID
    const selectedCat = categories.find(
      (cat) => cat._id === selectedCategoryId
    );
    const platformFee =
      selectedCat?.category_name === "OPERATIVE PROCEDURES" ? 999 : 99;

    setService({
      ...service,
      category: selectedCategoryId,
      selectedService: "",
      platformFees: platformFee,
    });

    console.log("ID",selectedCategoryId);
    

    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${selectedCategoryId}`
      );

      console.log("response",response.data.services);

      const servicesArray = Array.isArray(response.data)
        ? response.data
        : response.data
        ? [response.data]
        : [];

      setCategoryService({
        category: selectedCategoryId,
        services: servicesArray,
      });
    } catch (err) {
    console.error("Error fetching services:", err);
    if (err.response && err.response.status === 404) {
      setCategoryService({
        category: selectedCategoryId,
        services: [],
        message: err.response.data?.message || "No services found for this category",
      });
    } else {
   
      setCategoryService({
        category: selectedCategoryId,
        services: [],
        message: "Failed to fetch services. Please try again later.",
      });
    }
  }
};

const handleChange = (e) => {
  const { name, value, type, selectedOptions, multiple } = e.target;

  if (multiple && selectedOptions) {
    const values = Array.from(selectedOptions, (option) => option.value);
    setService((prev) => ({
      ...prev,
      [name]: values,
    }));
  } else {
    const newValue = type === "number" ? (value === "" ? "" : Number(value)) : value;

    setService((prev) => {
      const updated = { ...prev, [name]: newValue };

      // ✅ Adjust platform fee based on category
      if (
        (name === "category" && newValue === "OPERATIVE PROCEDURES") ||
        prev.category === "OPERATIVE PROCEDURES"
      ) {
        updated.platformFees = 999;
      } else if (name === "category") {
        updated.platformFees = 99;
      }

      const actual = name === "actualPrice" ? Number(newValue) : Number(updated.actualPrice);
      const discount =
        name === "discountedPrice" ? Number(newValue) : Number(updated.discountedPrice);
      const fees = Number(updated.platformFees) || 0;

      // ✅ New Logic
      if (!isNaN(discount) && discount > 0) {
        const total = discount + fees;

        if (actual && actual <= total) {
          alert(
            `Actual price can't be less than or equal to total price (₹${total})`
          );
          updated.discountedPrice = "";
          updated.totalPrice = actual + fees; // revert to actual if invalid
        } else {
          updated.totalPrice = total;
        }
      } else if (!isNaN(actual) && actual > 0) {
        // discount is empty or removed → use actual
        updated.totalPrice = actual + fees;
      } else {
        updated.totalPrice = "";
      }

      return updated;
    });
  }
};

  const handleShiftTimeChange = (index, field, value) => {
    const updated = [...service.shiftTimes];
    updated[index][field] = value;
    setService((prev) => ({
      ...prev,
      shiftTimes: updated,
    }));
  };

  const addShiftTime = () => {
    setService((prev) => ({
      ...prev,
      shiftTimes: [...prev.shiftTimes, { from: "", to: "" }],
    }));
  };

  const removeShiftTime = (index) => {
    const updated = service.shiftTimes.filter((_, i) => i !== index);
    setService((prev) => ({
      ...prev,
      shiftTimes: updated,
    }));
  };

const convertTo12Hour = (time) => {
    if (!time) return "";

    const [hour, minute] = time.split(":");
    const date = new Date();
    date.setHours(hour, minute, 0);

    return date.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      hour12: true,
    });
  };
  useEffect(() => {
    const fetchCentreDetails = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/${centreId}`
        );
        setStatus(res.data.status);
      } catch (error) {
        console.error("Error fetching centre details", error.message);
      }
    };
    fetchCentreDetails();
  }, [centreId]);

  const [confirmModal,setConfirmModal] = useState(false);

  const handleSubmit = (e) => {
  e.preventDefault();
  if (status !== "Approved") {
    toast.error(
      "Please Complete your profile details and wait for admin approval to start listing your services."
    );
    return;
  }
 setConfirmModal(true);
};

const modalRef = useRef(null);
    
        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              setConfirmModal
            ) {
              setConfirmModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // 🔑 Scroll lock logic
          if (confirmModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [confirmModal]);


const confirmSaveService = async () => {
  const payload = {
    serviceId: service.selectedService,
    actualPrice: service.actualPrice,
    discountPrice: service.discountedPrice,
    platformFees: service.platformFees,
    totalPrice: service.totalPrice,
    doctors: service.doctor,
    note: service.note,
    reportTiming: service.reportingTime,
    slots: service.shiftTimes.map((slot) => ({
      day: slot.day,
      from: convertTo12Hour(slot.from),
      to: convertTo12Hour(slot.to),
    })),
    description: service.description,
    centreId: centreId,
  };

  try {
    const response = await axios.post(
      `${process.env.REACT_APP_BACKEND_URL}/api/centreService/create`,
      payload
    );
    
    if (response.status === 201) {
      toast.success(
        response?.data?.message || "Service record saved successfully!"
      );
      
      // Reset form
      setService({
        category: "",
        selectedService: "",
        actualPrice: "",
        discountedPrice: "",
        totalPrice: "",
        platformFees: 99,
        note: "",
        shiftTimes: [{ day: "", from: "", to: "" }],
        reportingTime: "",
        doctor: [],
        description: "",
      });
      
      // Close modal after success
      setConfirmModal(false);
    }
  } catch (error) {
    console.error("Error saving service data:", error);
    toast.error(error?.response?.data?.message || "Failed to create service");
    
    // Close modal even on error (optional - remove if you want it to stay open)
    setConfirmModal(false);
  }
};

  return (
    <div className="dashboard-body__content">
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div className="card-body">
              <h3 className="mb-4">Add New Service</h3>
              {/* <h2>{formData ? "Edit Service" : "Create Service"}</h2> */}
              {status !== "Approved" && (
                <p className="text-danger">
                  Please complete your profile details and wait for admin
                  approval to start listing your services.
                </p>
              )}
              <form encType="multipart/form-data" onSubmit={handleSubmit}>
                <div className="row g-3">
                  {/* Category */}
                  <div className="col-md-6">
                    <label className="form-label">
                      Service Category <span className="text-danger">*</span>
                    </label>
                    <select
                      disabled={status !== "Approved"}
                      name="category"
                      value={service.category}
                      onChange={handleCategoryChange}
                      className="form-select"
                      required
                    >
                      <option value="" disabled selected>
                        Select a Category
                      </option>
                      {categories.map((cat) => (
                        <option key={cat.id} value={cat._id}>
                         
                          
                          {cat.category_name}
                        </option>
                      ))}
                    </select>
                  </div>
                 <div className="col-md-6">
  <label className="form-label">
    Service <span className="text-danger">*</span>
  </label>

  {categoryService.message ? (
    <p className="text-danger">{categoryService.message}</p>
  ) : (
    <Select
      name="selectedService"
      value={
        service.selectedService
          ? categoryService.services[0]?.services
              ?.map((serviceItem) => ({
                value: serviceItem._id,
                label: serviceItem.serviceName,
              }))
              ?.find(
                (option) => option.value === service.selectedService
              )
          : null
      }
      onChange={(selectedOption) =>
        handleChange({
          target: {
            name: "selectedService",
            value: selectedOption ? selectedOption.value : "",
          },
        })
      }
      options={
        Array.isArray(categoryService.services[0]?.services)
          ? categoryService.services[0].services.map((serviceItem) => ({
              value: serviceItem._id,
              label: serviceItem.serviceName,
            }))
          : []
      }
      placeholder="Select Service"
      isClearable={true}
      isDisabled={!service.category}
      isSearchable={true}
      required
      menuPortalTarget={document.body}
      styles={{
        menuPortal: (base) => ({ ...base, zIndex: 9999 }),
      }}
    />
  )}
</div>


                  {/* Actual Price */}
                  <div className="col-md-4">
                    <label className="form-label">
                      {selectedCategory?.category_name ===
                      "OPERATIVE PROCEDURES"
                        ? "Fees (₹)"
                        : "Actual Price (₹)"}
                      <span className="text-danger"> *</span>
                    </label>

                    <input
                      type="number"
                      name="actualPrice"
                      value={service.actualPrice}
                      required
                      onChange={handleChange}
                      className="form-control"
                      min="1"
                      placeholder="e.g. 500"
                    />
                  </div>

                  {/* Discounted Price */}
                  <div className="col-md-4">
                    <label className="form-label">
                      {selectedCategory?.category_name ===
                      "OPERATIVE PROCEDURES"
                        ? "Discounted Fees (₹)"
                        : "Discounted Price (₹)"}
                      {/* <span className="text-danger">*</span> */}
                    </label>
                    <input
                      type="number"
                      name="discountedPrice"
                      value={service.discountedPrice}
                      onChange={handleChange}
                      className="form-control"
                        min="1"
                      placeholder="e.g. 300"
                    />
                  </div>

                  <div className="col-md-4">
                    <label className="form-label">
                      {selectedCategory?.category_name ===
                      "OPERATIVE PROCEDURES"
                        ? "Total Fees (₹) Included ₹999 Platform Fees"
                        : "Total Price (₹)"}
                      {/* <span className="text-danger">*</span> */}
                    </label>
                    <input
                      type="number"
                      name="totalPrice"
                      value={service.totalPrice}
                      onChange={handleChange}
                      className="form-control"
                      required
                      placeholder="e.g. 800"
                      readOnly
                    />
                  </div>

                  {selectedCategory?.category_name ===
                  "OPERATIVE PROCEDURES" ? (
                    <div className="col-md-6">
                      <label className="form-label">
                        Note for Patient<span className="text-danger">*</span>
                      </label>
                      <input
                        type="text"
                        name="note"
                        value={service.note}
                        onChange={handleChange}
                        className="form-control"
                        placeholder="Write your note"
                        required
                      />
                    </div>
                  ) : (
                    <div className="col-md-6">
                      <p className="text-muted">
                        <span>Note: </span> ₹99 platform fee will be added and
                        is included in the total price visible to the patient.
                      </p>
                    </div>
                  )}

                  {/* Doctor */}
                  <div className="col-md-6">
                    <label className="form-label">Doctor <span className="text-danger">*</span>
</label>
                    <Select
                      isMulti
                      name="doctor"
                      value={selectedDoctorOptions}
                      required
                      onChange={handleDoctorChange}
                      options={doctorOptions}
                      placeholder="Select Doctor(s)..."
                      classNamePrefix="react-select"
                      menuPortalTarget={document.body}
                      styles={{
                        menuPortal: (base) => ({
                          ...base,
                          zIndex: 9999,
                        }),
                      }}
                    />
                  </div>

                  {/* Shift Time */}
                  <div className="col-md-12">
                    <label className="form-label">
                      Time Slots for Current week(You can add multiple date and
                      time)
                                            <span className="text-danger"> *</span>
                    </label>

                    {service.shiftTimes.map((slot, index) => (
                      <div
                        key={index}
                        className="row align-items-center mb-2 shift-row"
                      >
                        <div className="col-md-3">
                          <select
                            name="day"
                            className="form-control"
                            value={slot.day}
                            onChange={(e) =>
                              handleShiftTimeChange(
                                index,
                                "day",
                                e.target.value
                              )
                            }
                            required
                          >
                            <option value="" disabled selected>
                              Select a Day
                            </option>
                            <option value="Sunday">Sunday</option>
                            <option value="Monday">Monday</option>
                            <option value="Tuesday">Tuesday</option>
                            <option value="Wednesday">Wednesday</option>
                            <option value="Thursday">Thursday</option>
                            <option value="Friday">Friday</option>
                            <option value="Saturday">Saturday</option>
                          </select>
                        </div>
                        <div className="col-md-3">
                          <input
                            type="time"
                            value={slot.from}
                            onChange={(e) =>
                              handleShiftTimeChange(
                                index,
                                "from",
                                e.target.value
                              )
                            }
                            className="form-control"
                            placeholder="From Time"
                            required
                          />
                        </div>
                        <div className="col-md-3">
                          <input
                            type="time"
                            value={slot.to}
                            onChange={(e) =>
                              handleShiftTimeChange(index, "to", e.target.value)
                            }
                            className="form-control"
                            placeholder="To Time"
                            required
                          />
                        </div>
                        <div className="col-md-2">
                          <button
                            type="button"
                            className="btn btn-danger"
                            onClick={() => removeShiftTime(index)}
                            disabled={service.shiftTimes.length === 1}
                          >
                            Remove
                          </button>
                        </div>
                      </div>
                    ))}

                    <button
                      type="button"
                      className="btn btn-success mt-2"
                      onClick={addShiftTime}
                    >
                      + Add Day/Time
                    </button>
                  </div>

                  <div className="col-md-12">
                    <label className="form-label">
                      Doctor Report Uploading Time
                      <span className="text-danger"> *</span>
                    </label>
                    <input
                      type="text"
                      name="reportingTime"
                      value={service.reportingTime}
                      className="form-control"
                      placeholder="Write a note"
                      onChange={handleChange}
                      required
                    />
                    <small className="form-text text-muted">
                      Example: You will get your report within 2 hours after
                      checkup.
                    </small>
                  </div>

                  {/* Description */}
                  <div className="col-md-12">
                    <label className="form-label">
                      Description of services provided if any 
                                            <span className="text-danger"> *</span>

                    </label>
                    <textarea
                      name="description"
                      value={service.description}
                      onChange={handleChange}
                      className="form-control"
                      placeholder="Description about service"
                      rows="3"
                      required
                    ></textarea>
                  </div>
                </div>

                <div className="mt-4">
                  <button
                    type="submit"
                    className="btn btn-main"
                    style={{
                      backgroundColor: "green",
                      color: "whitesmoke",
                      padding: "10px 20px",
                      borderRadius: "5px",
                      fontWeight: "600",
                    }}
                  >
                    Save Service
                  </button>
                </div>
              </form>
            </div>
          </div>

 {confirmModal && (
  <div
    className="modal-backdrop"
    style={{
      position: "fixed",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      backgroundColor: "rgba(0,0,0,0.5)",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      zIndex: 9999,
    }}
    onClick={(e) => {
      // Close modal when clicking outside
      if (e.target.className === 'modal-backdrop') {
        setConfirmModal(false);
      }
    }}
  >
    <div
      className="modal-content"
      style={{
        backgroundColor: "white",
        padding: "20px",
        borderRadius: "8px",
        width: "600px",
        maxWidth: "90%",
        textAlign: "center",
      }}
      onClick={(e) => e.stopPropagation()} // Prevent closing when clicking inside
    >
      <h5>Please verify the details you filled</h5>
      <p>Do you want to create the service?</p>
      <div className="mt-3" style={{ display: "flex", justifyContent:"center", gap:"10px" }}>
        <button
          className="btn btn-success"
          onClick={() => confirmSaveService()}
        >
          Yes
        </button>
        <button
          className="btn btn-danger"
          onClick={() => setConfirmModal(false)}
        >
          No
        </button>
      </div>
    </div>
  </div>
)}

        </div>
      </div>
      <style>
        {`@media (max-width: 576px) {
  .shift-row {
    gap: 10px; /* vertical spacing between inputs */
  }
}
          `}
      </style>

    

<style>
{`
  @media (max-width: 625px) {
    /* Make the card full width with some padding */
    .modal-backdrop {
     
      margin: 12px auto !important;
      padding: 15px !important;
    }

    .shift-row {
      flex-direction: column !important;
      gap: 10px;
    }

    .col-md-4,
    .col-md-6,
    .col-md-12 {
      flex: 0 0 100% !important;
      max-width: 100% !important;
    }

    .btn {
     
      margin-top: 5px !important;
    }

    /* Optional: reduce font size on small screens */
    .card-body {
      font-size: 14px;
    }
  }
`}
</style>
  <ToastContainer position="top-right" autoClose={3000} />




    </div>
 
  );
};

export default CreateServiceLayer;
